<script>
    import baseUrl from '../base/baseUrl.js'
    import axios from 'axios';
    import countryArr from '../country.js';
    import militaryArr from '../military.js';
    import nation from '../nation.js';
    import area from '../area.js';
    export default {
        data(){
            return{
                msg: '',
                deviceInput: '',
                deviceNum: true,//disabled
                tipMsg: '',//
                loading: false,//loading
                dialogVisible: false,

                // 
                cardTypeArr: ["",],//"",""
                militaryArr: militaryArr,//
                // marryArr: ["","","","",""],//
                marryArr: [
                    {name:"YH",value:""},{name:"WH",value:""},{name:"ZH",value:""},
                        {name:"SO",value:""},{name:"QT",value:""}
                ],
                marryArr_new: [
                    {name:"YH",value:""},{name:"WH",value:""},{name:"ZH",value:""},
                        {name:"SO",value:""},{name:"QT",value:""}
                ],
                // areaArr: ["","","","","","","","","","","",
                //         "","","","","","","","","","","","",
                //         "","",""," ","","","","","","","","",
                //         "","","","","", ""  ],//
