                    {name:"QYGLRY",value:""},{name:"GR",value:""},{name:"NM",value:""},
                    {name:"XS",value:""},{name:"XYJR",value:""},{name:"ZYZYZ",value:""},
                    {name:"GTJYZ",value:""},{name:"WYRY",value:""},{name:"TXRY",value:""},
                    {name:"QT",value:""},],
                jobArr_new: [{name:"GJGWY",value:""},{name:"ZYJSRY",value:""},{name:"ZY",value:""},
                    {name:"QYGLRY",value:""},{name:"GR",value:""},{name:"NM",value:""},
                    {name:"XS",value:""},{name:"XYJR",value:""},{name:"ZYZYZ",value:""},
                    {name:"GTJYZ",value:""},{name:"WYRY",value:""},{name:"TXRY",value:""},
                    {name:"QT",value:""},],
                hospitalArr: [],//
                hospitalArr_new: [],//

                sexArr: [{name:"N",value:""},{name:"N",value:""}],
                sexArr_new: [{name:"N",value:""},{name:"N",value:""}],
                nationArr: nation,//
                nationArr_new: nation,
                areaArr: area,
                areaArr_new: area,

                countryArr: countryArr,
                countryArr_new: countryArr,
                // placeholder
                idCardPlaceholder: '',//

