                name: '',
                sex: '',
                idCard: '',
                cardType: '',
                military: '',
                country: '',
                selectedMarry: '',
                countryList: '',
                age: '',
                birthday: '',
                nation: '',
                address: '',
                area: '',
                job: '',
                phone: '',
                icCard: '',
                service: '',
                fkPerson: '',
                orderNum: '',
                origin: '',
                hospital: '',
                opName: '',
                opNo: '',
                createTime: '',


                // 
                militaryShow: false,
                countryShow: true,
                countrySelectShow: false,
                idCardImg:  require('../../assets/background_idcard.png'),
                faceImg: require("../../assets/background_faceid.png"),

